.formWrapper {
    display: flex;
    justify-content: center;
    padding: 40px 16px;
    background-color: #ffffff;
    margin-top: 30px;
  }
  
 
  @media screen and (max-width: 480px) {
    .formWrapper {
        margin-top: 20px;
      padding: 20px 12px;
    }
  }
  

  @media screen and (min-width: 768px) {
    .formWrapper {
      padding: 60px 24px;
      margin-top: 50px;
    }
  }
.form {
    display: flex;
    flex-direction: column;
    gap: 16px;
    max-width: 400px;
    width: 100%;
    padding: 20px;
    margin: 0 auto;
    border: 1px solid #ccc;
    border-radius: 12px;
    background-color: #f9f9f9;
  }
  
  .label {
    display: flex;
    flex-direction: column;
    font-weight: 500;
    color: #333;
  }
  
  .input {
    padding: 10px;
    border: 1px solid #bbb;
    border-radius: 6px;
    font-size: 16px;
    margin-top: 4px;
  }
  
  .button {
    padding: 12px;
    background-color: #4caf50;
    color: white;
    border: none;
    font-size: 16px;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .button:hover {
    background-color: #45a049;
  }
  

  @media screen and (max-width: 480px) {
    .form {
      padding: 16px;
      gap: 12px;
    }
  
    .input,
    .button {
      font-size: 15px;
      padding: 10px;
    }
  }
  

  @media screen and (min-width: 768px) {
    .form {
      max-width: 500px;
    }
  
    .input {
      font-size: 18px;
    }
  
    .button {
      font-size: 18px;
    }
  }
  